.preload {
    width: 100%;
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: #fff;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 300;
    transition: all 1.5s cubic-bezier(1, -0.36, .35, 1.27);
    &.is-loaded {
        transform: translate3d(0, 0, 0);
        opacity: 0;
    }
    &__text {
        font-weight: 100;
        color: #999;
        left: 50%;
        top: 50%;
        transform: translate3d(-50%,140%,0);
        position: absolute;
        z-index: 20;
        opacity: 1;
        font-size: 28px;
        span {
            font-size: 16px;
        }
    }
    &__logo {
        width: 153px;
        height: 58px;
        z-index: 10;
        background: transparent url(../images/gt-logo.png) no-repeat 0 0;
        position: absolute;
        background-size: 153px 58px;
        top: 50%;
        left: 50%;
        transform: translate3d(-50%,-50%,0);
    }
}

