.vol2-1-kv {
    width: 100%;
    height: 754px;
    background: #000 url(../images/vol2/vol2-1-kv-bg.jpg) no-repeat center top;
    position: relative;
}

.vol2-1-skill-1 {
    width: 100%;
    height: 733px;
    background: #000 url(../images/vol2/vol2-1-skill-1-bg.jpg) no-repeat center top;
    position: relative;
    &__h3 {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(13%,-320%,0);
        z-index: 10;
        img {
            height: 62px;
            display: block;
            margin: 0;
        }
    }
    &__video {
        background-color: #000;
        box-sizing: border-box;
        width: 450px;
        height: 255px;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-21%,-50%,0);
        border: solid 2px #f65c5c;
        box-shadow: 0px 1px 9.88px 3.12px rgba(0, 0, 0, 0.4);
        z-index: 10;
    }
    &__light {
        width: 100%;
        height: 733px;
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate3d(-50%,0,0);
        background: transparent url(../images/vol2/vol2-1-skill-1-light.png) no-repeat center center;
        z-index: 5;
        animation-name: vol21light;
        animation-duration: 1.5s;
        animation-iteration-count: infinite;
        animation-timing-function: ease-in-out;
        animation-fill-mode: both;
        animation-direction: alternate;
        opacity: 0;
        transform: translate3d(-50%,0,0);
    }
}

@keyframes vol21light {
    from {
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}

