.vol2-1-sns {
    overflow: hidden;
    width: 100%;
    height: 692px;
    background: #000 url(../images/vol2/vol2-1-sns-bg.jpg) no-repeat center top;
    position: relative;
    overflow: hidden;
    &__h3 {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-11%,-520%,0);
        z-index: 10;
        img {
            height: 62px;
            display: block;
            margin: 0;
        }
    }
    &__cht {
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate3d(-50%,0,0);
        z-index: 10;
        pointer-events: none;
    }
    &__flag-wrap {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-9%,-40%,0);
        width: 413px;
        height: 266px;
    }
    &__light {
        position: absolute;
        left: 50%;
        top: 50%;
        transform-origin: 0 0;
        transform: translate3d(0,0,0) rotate(0);
        animation-name: vol24light;
        animation-duration: 5s;
        animation-iteration-count: infinite;
        animation-timing-function: ease-in-out;
        animation-fill-mode: both;
        animation-direction: alternate;
        margin-left: -450px;
        margin-top: -500px;
    }
    &__btn {
        display: block;
        position: absolute;
        img {
            &:nth-child(1) {
                max-width: 100%;
                display: block;
                margin: 0;
                position: relative;
                z-index: 0;
            }
            &:nth-child(2) {
                position: absolute;
                left: 0;
                top: 0;
                z-index: 1;
                opacity: 0;
                transition: opacity 0.5s ease-in-out;
            }
        }
        &:hover {
            img {
                &:nth-child(2) {
                    opacity: 1;
                }
            }
        }
        &--1 {
            left: 0;
            top: 0;
        }
        &--2 {
            left: 98px;
            top: 0;
        }
        &--3 {
            left: 328px;
            top: 0;
        }
        &--4 {
            left: 98px;
            top: 74px;
        }
        &--5 {
            left: 0px;
            top: 190px;
        }
        &--6 {
            left: 214px;
            top: 190px;
        }
    }
}

@keyframes vol24light {
    from{
        transform: translate3d(0,0,0) rotate(0);
    }
    to{
        transform: translate3d(0,0,0) rotate(15deg);
    }
}

