.right-nav {
    width: 180px;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate3d(230%,-50%,0);
    background: transparent url(../images/right-nav-bg@2x.png) no-repeat 0 0;
    background-size: 39px 167px;
    padding-left: 52px;
    z-index: 40;
    @include susy-breakpoint($s-320, $c8) {
        display: none;
    }
    a {
        margin-bottom: 24px;
        line-height: 40px;
        height: 40px;
        position: relative;
        display: block;
        font-size: 24px;
        color: #979797;
        font-weight: bolder;
        text-align: left;
        text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.5);
        transition: opacity 0.4s ease-in-out;
        &::before {
            width: 30px;
            height: 30px;
            background: transparent url(../images/right-nav-active@2x.png) no-repeat 0 0;
            background-size: 30px;
            position: absolute;
            left: 0;
            top: 0;
            transform: translate3d(-158%,5px,0);
            content: "";
            opacity: 0;
            transition: opacity 0.4s ease-in-out;
        }
        &:hover {
            color: #e400ff;
            &::before {
                opacity: 1;
            }
        }
        &.is-active {
            color: #e400ff;
            &::before {
                opacity: 1;
            }
        }
    }
    &--vol1-1 {
        background-image: url(../images/right-nav-vol1-bg@2x.png);
        background-size: 39px 231px;
        a {
            color: rgb(151, 151, 151);
            text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.5);
            &::before {
                background-image: url(../images/right-nav-vol1-1-active@2x.png);
            }
            &:hover {
                color: #fdb23d;
            }
            &.is-active {
                color: #fdb23d;
            }
        }
    }
    &--vol1-2 {
        background-image: url(../images/right-nav-vol1-bg@2x.png);
        background-size: 39px 231px;
        a {
            color: rgb(151, 151, 151);
            text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.5);
            &::before {
                background-image: url(../images/right-nav-vol1-2-active@2x.png);
            }
            &:hover {
                color: #ff77c5;
            }
            &.is-active {
                color: #ff77c5;
            }
        }
    }
    &--vol2-1 {
        background-image: url(../images/right-nav-vol1-bg@2x.png);
        background-size: 39px 231px;
        a {
            color: rgb(151, 151, 151);
            text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.5);
            &::before {
                background-image: url(../images/right-nav-vol2-1-active@2x.png);
            }
            &:hover {
                color: #ff0000;
            }
            &.is-active {
                color: #ff0000;
            }
        }
    }
    &--vol2-2 {
        background-image: url(../images/right-nav-vol1-bg@2x.png);
        background-size: 39px 231px;
        a {
            color: rgb(151, 151, 151);
            text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.5);
            &::before {
                background-image: url(../images/right-nav-vol2-2-active@2x.png);
            }
            &:hover {
                color: #357af9;
            }
            &.is-active {
                color: #357af9;
            }
        }
    }
    &--vol3-1 {
        background-image: url(../images/right-nav-vol1-bg@2x.png);
        background-size: 39px 231px;
        a {
            color: rgb(151, 151, 151);
            text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.5);
            &::before {
                background-image: url(../images/right-nav-vol3-1-active@2x.png);
            }
            &:hover {
                color: #6df324;
            }
            &.is-active {
                color: #6df324;
            }
        }
    }
    &--vol3-2 {
        background-image: url(../images/right-nav-vol3-bg@2x.png);
        background-size: 39px 295px;
        a {
            color: rgb(151, 151, 151);
            text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.5);
            &::before {
                background-image: url(../images/right-nav-vol3-2-active@2x.png);
            }
            &:hover {
                color: #ffbe22;
            }
            &.is-active {
                color: #ffbe22;
            }
        }
    }
}

@keyframes flashLight {
    0% {
        background-position-x: 0;
    }

    100% {
        background-position-x: 244.3px;
    }
}

.s-header {
    position: fixed;
    width: 100%;
    left: 0;
    top: 40px;
    // background: transparent url(../images/nav-bg.png) no-repeat center top;
    background-color: rgba(0,0,0,0.8);
    border-bottom: 2px solid #35303a;
    height: 152px;
    z-index: 40;
    transition: top 0.1s ease-in-out;
    @include susy-breakpoint($s-320, $c8) {
        top: 0;
    }
    &.is-scroll {
        top: 0;
    }
    &__h1 {
        position: absolute;
        left: 10px;
        top: 10px;
        a {
            display: block;
        }
        img {
            height: 60px;
            display: block;
            margin: 0;
        }
    }
    &__h2 {
        font-size: 31px;
        color: #eac27e;
        text-align: center;
        padding-top: 22px;
        a {
            background: linear-gradient(90deg,#eac27e 30%,#fff 50%,#eac27e 70%);
            color: #eac27e;
            transition: all 0.4s ease-in-out;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            animation-name: flashLight;
            animation-duration: 1s;
            animation-iteration-count: infinite;
            animation-timing-function: linear;
            animation-fill-mode: both;
            animation-direction: normal;
            &:hover {
                color: #fff;
            }
        }
    }
    &__btn-event {
        position: absolute;
        font-size: 31px;
        color: #979797;
        right: 16px;
        top: 22px;
        font-weight: bolder;
        transition: all 0.4s ease-in-out;
        &:hover {
            color: #fff;
        }
    }
    &__nav {
        text-align: center;
        padding-top: 36px;
    }
    &__btn {
        display: inline-block;
        width: 75px;
        height: 31px;
        margin-right: 48px;
        position: relative;
        &:last-child {
            margin-right: 0;
        }
        img {
            &:nth-child(1) {
                position: relative;
                z-index: 0;
                max-width: 100%;
                display: block;
                transition: opacity 0.5s ease-in-out;
            }
            &:nth-child(2) {
                width: 94px;
                height: 83px;
                position: absolute;
                z-index: 0;
                left: 50%;
                top: 50%;
                transform: translate3d(-50%,-60%,0);
                display: block;
                opacity: 0;
                transition: all 0.5s ease-in-out;
            }
        }
        &:hover {
            img {
                &:nth-child(1) {
                    opacity: 0;
                }
                &:nth-child(2) {
                    opacity: 1;
                    transform: translate3d(-50%,-50%,0);
                }
            }
        }
        &.is-active {
            pointer-events: none;
            img {
                &:nth-child(1) {
                    opacity: 0;
                }
                &:nth-child(2) {
                    opacity: 1;
                    transform: translate3d(-50%,-50%,0);
                }
            }
        }
    }
}

.s-main {
    position: relative;
    z-index: 10;
    width: 100%;
    overflow: hidden;
}

.inner {
    position: relative;
    width: 750px;
    margin: 0 auto;
}

.vol-nav {
    z-index: 10;
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    height: 140px;
    background-color: rgba(0,0,0,0.7);
    border-top: solid 1px rgba(#d0d0d0,0.4);
    padding-top: 30px;
    box-sizing: border-box;
    h3 {
        text-align: center;
        color: #d0d0d0;
        font-size: 28px;
        font-weight: bold;
        text-shadow: 0px 1px 0.43px rgba(0, 0, 0, 0.4);
        margin-bottom: 18px;
    }
    &__nav {
        text-align: center;
        position: relative;
        height: 43px;
        &::after {
            width: 2px;
            height: 43px;
            position: absolute;
            left: 50%;
            top: 0;
            transform: translate3d(-50%,0,0);
            content: "";
            background-color: #b4b4b4;
            box-shadow: 0px 1px 0.43px rgba(0, 0, 0, 0.4);
        }
        a {
            margin: 0 50px;
            height: 43px;
            line-height: 40px;
            display: inline-block;
            font-size: 40px;
            color: #ababab;
            text-shadow: 0px 1px 0.43px rgba(0, 0, 0, 0.4);
            transition: all 0.3s ease-in-out;
            font-weight: bolder;
            text-shadow: 0 1px 0 rgba(0,0,0,0.9);
            &:nth-child(1) {
                &:hover {
                    color: #ffa81f;
                }
            }
            &:nth-child(2) {
                &:hover {
                    color: #fd34e3;
                }
            }
            &.is-active {
                &:nth-child(1) {
                    color: #ffa81f;
                }
                &:nth-child(2) {
                    color: #fd34e3;
                }
            }
        }
        &--1 {
            a {
                &:nth-child(1) {
                    &:hover {
                        color: #ffa81f;
                    }
                }
                &:nth-child(2) {
                    &:hover {
                        color: #fd34e3;
                    }
                }
                &.is-active {
                    &:nth-child(1) {
                        color: #ffa81f;
                    }
                    &:nth-child(2) {
                        color: #fd34e3;
                    }
                }
            }
        }
        &--2 {
            a {
                &:nth-child(1) {
                    &:hover {
                        color: #ff0000;
                    }
                }
                &:nth-child(2) {
                    &:hover {
                        color: #4d66ff;
                    }
                }
                &.is-active {
                    &:nth-child(1) {
                        color: #ff0000;
                    }
                    &:nth-child(2) {
                        color: #4d66ff;
                    }
                }
            }
        }
        &--3 {
            a {
                &:nth-child(1) {
                    &:hover {
                        color: #87d018;
                    }
                }
                &:nth-child(2) {
                    &:hover {
                        color: #f3c047;
                    }
                }
                &.is-active {
                    &:nth-child(1) {
                        color: #87d018;
                    }
                    &:nth-child(2) {
                        color: #f3c047;
                    }
                }
            }
        }
    }
}

.vol-btn-top {
    position: absolute;
    width: 409px;
    height: 446px;
    left: 50%;
    top: 1229px;
    z-index: 20;
    transform: translate3d(2%,0,0);
    display: block;
    transition: all 0.3s ease-in-out;
    &.is-active {
        opacity: 1;
        pointer-events: auto;
    }
    img {
        &:nth-child(1) {
            width: 100%;
            position: relative;
            z-index: 0;
            pointer-events: none;
        }
        &:nth-child(2) {
            position: absolute;
            width: 100%;
            top: 0;
            left: 0;
            opacity: 0;
            pointer-events: none;
            z-index: 1;
        }
    }
    &:hover {
        img {
            &:nth-child(1) {
                opacity: 0;
            }
            &:nth-child(2) {
                opacity: 1;
            }
        }
    }
}

.sns-tips {
    z-index: 30;
    padding: 24px 16px;
    font-size: 24px;
    color: #000;
    border-radius: 20px;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate3d(-90%,120%,0);
    background-color: #fff;
    font-weight: bolder;
    box-shadow: 0 0 10px rgba(0,0,0,0.3);
    animation-name: snsTipAni;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    animation-fill-mode: both;
    animation-direction: alternate-reverse;
    &::after {
        position: absolute;
        content: "";
        left: 50%;
        bottom: 0;
        width: 0; 
        height: 0; 
        border-left: 16px solid transparent;
        border-right: 16px solid transparent;
        border-top: 16px solid #fff;
        bottom: -15px;
        transform: translate3d(-170%,0,0);
    }
    &--vol1-1 {
        color: #f0ac35;
        margin-left: 10px;
    }
    &--vol1-2 {
        color: #f476ae;
        margin-left: 30px;
    }
    &--vol2-1 {
        color: #da2144;
    }
    &--vol2-2 {
        color: #4c8ffe;
    }
    &--vol3-1 {
        color: #92da21;
        margin-left: 30px;
    }
    &--vol3-2 {
        color: #f2930c;
        margin-left: 80px;
    }
}

@keyframes snsTipAni {
    from{
        transform: translate3d(-90%,120%,0);
    }
    to{
        transform: translate3d(-90%,130%,0);
    }
}
