.vol1-btn-top {
    position: absolute;
    width: 319px;
    height: 308px;
    left: 50%;
    top: 1300px;
    z-index: 20;
    transform: translate3d(30%,0,0);
    display: block;
    transition: all 0.3s ease-in-out;
    &.is-active {
        opacity: 1;
        pointer-events: auto;
    }
    img {
        &:nth-child(1) {
            width: 100%;
            position: relative;
            z-index: 0;
            pointer-events: none;
        }
        &:nth-child(2) {
            position: absolute;
            width: 100%;
            top: 0;
            left: 0;
            opacity: 0;
            transition: all 0.3s ease-in-out;
            pointer-events: none;
            z-index: 1;
        }
    }
    &:hover {
        img {
            &:nth-child(2) {
                opacity: 1;
            }
        }
    }
}

.vol1-1-kv {
