.home-kv {
    width: 100%;
    background: transparent url(../images/home-kv-3@2x.png) no-repeat center -10%;
    padding-top: 614px;
    position: relative;
    padding-bottom: 224px;
    transition: all 1s ease-in-out;
    opacity: 0;
    &.is-active {
        background-position: center 0;
        opacity: 1;
    }
    &__h2 {
        width: 579px;
        height: 156px;
        position: absolute;
        left: 50%;
        top: 0;
        transform: translate3d(-50%,276%,0);
        transition: all 1s ease-in-out 1s;
        opacity: 0;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
        &.is-active {
            opacity: 1;
            transform: translate3d(-50%,296%,0);
        }
    }
    &__video {
        width: 578px;
        height: 326px;
        background-color: #000;
        box-shadow: 0 0 16px #000;
        margin: 0 auto;
        position: relative;
        &::after {
            background: transparent url(../images/home-video-border@2x.png) no-repeat center top;
            background-size: cover;
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
            z-index: 100;
            pointer-events: none;
            content: "";
        }
    }
    &__video-wrap {
        position: relative;
        z-index: 0;
    }
    &__monster-left {
        width: 195px;
        height: 327px;
        position: absolute;
        left: 50%;
        transform: translate3d(-243%,15%,0);
        bottom: 0;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
    }
    &__btn-monster-right {
        width: 311px;
        height: 324px;
        display: block;
        position: absolute;
        left: 50%;
        transform: translate3d(26%,24%,0);
        bottom: 0;
        z-index: 120;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
    }
}

.home-cht {
    padding-bottom: 490px;
    position: relative;
    &__h2 {
        text-align: center;
        img {
            width: 350px;
            height: 70px;
            display: inline-block;
            margin: 0;
        }
    }
    &__slide-wrap {
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate3d(-50%,0,0);
        width: 100%;
        height: 520px;
        margin: 0 auto;
    }
    .swiper-button-prev {
        width: 29px;
        height: 60px;
        position: absolute;
        left: 50%;
        transform: translate3d(-1280%,0,0);
        background: transparent url(../images/btn-arrow-left@2x.png) no-repeat 0 0;
        background-size: 29px 60px;
        font-size: 0;
        color: transparent;
    }
    .swiper-button-next {
        width: 29px;
        height: 60px;
        position: absolute;
        left: 50%;
        transform: translate3d(1180%,0,0);
        background: transparent url(../images/btn-arrow-right@2x.png) no-repeat 0 0;
        background-size: 29px 60px;
        font-size: 0;
        color: transparent;
    }
    .swiper-slide {
        position: relative;
        width: 100%;
        overflow: hidden;
        height: 520px;
        img {
            display: block;
            width: 869px;
            height: 513px;
            position: absolute;
            left: 50%;
            bottom: 0;
            transform: translate3d(-50%,0,0);
        }
    }
}

.home-video-bg {
    position: absolute;
    width: 1920px;
    height: 1726px;
    left: 50%;
    top: 0;
    transform: translate3d(-50%,0,0);
    z-index: 0;
    pointer-events: none;
}

.home-master-out-slider {
    width: 1920px;
    height: 1008px;
    overflow: hidden;
    left: 50%;
    transform: translate3d(-50%,0,0);
}

.home-master {
    position: relative;
    &__h3 {
        position: absolute;
        top: 100px;
        left: 50%;
        transform: translate3d(-50%,0,0);
        text-align: center;
        z-index: 10;
        img {
            width: 350px;
            height: 71px;
        }
    }
    &__slider-wrap {
        .swiper-pagination--master {
            box-sizing: border-box;
            padding: 0 36px;
            width: 500px;
            height: 60px;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate3d(-35.5%,446%,0);
            text-align: left;
        }
        .swiper-pagination-bullet {
            margin: 0 26px !important;
            width: 20px;
            height: 20px;
            background-color: #000;
            border-radius: 0;
            opacity: 1;
            transform-origin: 50%;
            transform: rotate(45deg);
            position: relative;
            &::after {
                content: "";
                background: transparent url(../images/master-pagination@2x.png) no-repeat 0 0;
                background-size: 40px 40px;
                width: 41px;
                height: 41px;
                left: 50%;
                top: 50%;
                position: absolute;
                transform: translate3d(-50%,-50%,0) rotate(-45deg);
                contain: "";
                opacity: 0;
                transition: all 0.4s ease-in-out;
            }
        }
        .swiper-pagination-bullet-active {
            &::after {
                opacity: 1;
            }
        }
    }
}

.home-master-scene {
    background: #000 url(../images/home-master-scene-1-bg.jpg) no-repeat center top;
    width: 100%;
    height: 1008px;
    overflow: hidden;
    &--2 {
        background-image: url(../images/home-master-scene-2-bg.jpg);
    }
    &--3 {
        background-image: url(../images/home-master-scene-3-bg.jpg);
    }
    &__h4 {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-61%,-194%,0);
        img {
            width: 516px;
            height: 87px;
        }
    }
    &__cht {
        width: 1920px;
        height: 1008px;
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate3d(-50%,0,0);
        z-index: 0;
        pointer-events: none;
        overflow: hidden;
        img {
            width: 100%;
        }
    }
    &__item {
        width: 183px;
        height: 188px;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d( 98%,-124%,0);
        z-index: 10;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
    }
    &__slide-wrap {
        z-index: 5;
        width: 658px;
        height: 384px;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-39%,-26%,0);
        .swiper-container {
            text-align: center;
            img {
                display: inline-block;
                margin: 0;
            }    
        }
        .swiper-button-prev-scene {
            width: 36px;
            height: 69px;
            position: absolute;
            left: 50%;
            transform: translate3d(-820%,-15%,0);
            background: transparent url(../images/btn-arrow-left-scene@2x.png) no-repeat 0 0;
            background-size: 36px 69px;
            font-size: 0;
            color: transparent;
        }
        .swiper-button-next-scene {
            width: 36px;
            height: 69px;
            position: absolute;
            left: 50%;
            transform: translate3d(724%,-15%,0);
            background: transparent url(../images/btn-arrow-right-scene@2x.png) no-repeat 0 0;
            background-size: 36px 69px;
            font-size: 0;
            color: transparent;
        }
    }
}