.vol3-1-kv {
    width: 100%;
    height: 754px;
    background: #000 url(../images/vol3/vol3-1-kv-bg.jpg) no-repeat center top;
    position: relative;
}

.vol3-1-skill-1 {
    width: 100%;
    height: 733px;
    background: #000 url(../images/vol3/vol3-1-skill-1-bg.jpg) no-repeat center top;
    position: relative;
    &__h3 {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(13%,-320%,0);
        z-index: 10;
        img {
            height: 62px;
            display: block;
            margin: 0;
        }
    }
    &__video {
        background-color: #000;
        box-sizing: border-box;
        width: 450px;
        height: 255px;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-21%,-50%,0);
        border: solid 2px #8cf85c;
        box-shadow: 0px 1px 9.88px 3.12px rgba(0, 0, 0, 0.4);
        z-index: 10;
    }
    &__flower {
        width: 159px;
        height: 140px;
        position: absolute;
        top: 0;
        left: 50%;
        background: transparent url(../images/vol3/vol3-1-skill-1-flower@2x.png) no-repeat center center;
        background-size: cover;
        z-index: 10;
        transform: translate3d(163%,122%,0);
    }
}

