.vol3-1-sns {
    overflow: hidden;
    width: 100%;
    height: 692px;
    background: #000 url(../images/vol3/vol3-1-sns-bg.jpg) no-repeat center top;
    position: relative;
    overflow: hidden;
    &__h3 {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(18%,-520%,0);
        z-index: 10;
        img {
            height: 62px;
            display: block;
            margin: 0;
        }
    }
    &__cht {
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate3d(-50%,0,0);
        z-index: 10;
        pointer-events: none;
    }
    &__round-wrap {
        width: 1057px;
        height: 1057px;
        position: absolute;
        left: 50%;
        top: 50%;
        padding-top: 0;
        box-sizing: border-box;
        transform: translate3d(-67%,-44%,0);
        z-index: 0;
        clip-path: polygon(74% 18%, 100% 14%, 100% 100%, 73% 95%, 53% 79%, 45% 55%, 52% 36%);
    }
    &__round {
        width: 1057px;
        height: 1057px;
        margin: 0 auto;
    }
    &__btn {
        width: 541px;
        height: 133px;
        position: absolute;
        display: block;
        left: 50%;
        top: 50%;
        transform-origin: 2% 80%;
        img {
            &:nth-child(1) {
                max-width: 100%;
                display: block;
                margin: 0;
                position: relative;
                z-index: 0;
            }
            &:nth-child(2) {
                position: absolute;
                left: 0;
                top: 0;
                z-index: 1;
                opacity: 0;
                transition: opacity 0.5s ease-in-out;
            }
        }
        &:hover {
            img {
                &:nth-child(2) {
                    opacity: 1;
                }
            }
        }
        &--1 {
            transform: translate3d(-2%,-80%,0);
        }
        &--2 {
            transform: translate3d(-2%,-80%,0) rotate(30deg);
        }
        &--3 {
            transform: translate3d(-2%,-80%,0) rotate(60deg);
        }
        &--4 {
            transform: translate3d(-2%,-80%,0) rotate(90deg);
        }
        &--5 {
            transform: translate3d(-2%,-80%,0) rotate(120deg);
        }
        &--6 {
            transform: translate3d(-2%,-80%,0) rotate(150deg);
        }
        &--7 {
            transform: translate3d(-2%,-80%,0) rotate(180deg);
        }
        &--8 {
            transform: translate3d(-2%,-80%,0) rotate(210deg);
        }
        &--9 {
            transform: translate3d(-2%,-80%,0) rotate(240deg);
        }
        &--10 {
            transform: translate3d(-2%,-80%,0) rotate(270deg);
        }
        &--11 {
            transform: translate3d(-2%,-80%,0) rotate(300deg);
        }
        &--12 {
            transform: translate3d(-2%,-80%,0) rotate(330deg);
        }
    }
}

